<!-- Êú™Êù•ÊîπÂä® -->
<!-- Â¢ûÂä†Ê∏ÖÈô§ÂÜÖÂÆπÊåâÈíÆ -->
<!-- Â¢ûÂä†ÊòæÁ§∫ÂØÜÁ†ÅÊåâÈíÆ -->

<template>
    <!-- ÁªÑ‰ª∂‰∏ª‰Ωì -->
    <div class="input-group" 
        :class="[
            'status-' + status,
            { 'is-disabled': disabled },
            { 'is-always-active': alwaysActive }
        ]" 
        :style="{ width: moduleWidth, height: moduleHeight }"
    >
        <!-- ËæìÂÖ•Ê°Ü -->
        <input
            class="input"
            :id="uuid"
            :type="type"
            :value="modelValue"
            :disabled="disabled"
            @input="$emit('update:modelValue', $event.target.value)"
            @focus="$emit('focus', $event)"
            @blur="$emit('blur', $event)"
            autocomplete="off"
            required=""
        >
        <!-- ÊèêÁ§∫Ê†áÁ≠æ1 -->
        <label class="title1" :for="uuid">
            {{ title1 }}
        </label>
        <!-- ÊèêÁ§∫Ê†áÁ≠æ2 -->
        <label class="title2" :for="uuid">
            {{ title2 }}
        </label>
        <!-- ËæπÊ°ÜÂç†‰Ωç -->
        <label class="border-gap">
            {{ title2 }}
        </label>
        <div class="clear-btn" >
            <svg>
                <use xlink:href="#icon-DeleteIcon"></use>
            </svg>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'InputBox1',
        props: {
            // ÁªëÂÆöÂÄº
            modelValue: String,
            type: {
                type: String,
                default: ''
            },
            // ÊèêÁ§∫ÊñáÊú¨1
            title1: {
                type: String,
                default: ''
            },
            // ÊèêÁ§∫ÊñáÊú¨2
            title2: {
                type: String,
                default: ''
            },
            // ÁªÑ‰ª∂ÂÆΩÂ∫¶
            moduleWidth: {
                type: String,
                default: '10rem'
            },
            // ÁªÑ‰ª∂È´òÂ∫¶
            moduleHeight: {
                type: String,
                default: '3rem'
            },
            // ÁªÑ‰ª∂Áä∂ÊÄÅ
            status: {
                type: String,
                default: 'default'
            },
            // Á¶ÅÁî®Áä∂ÊÄÅ
            disabled: {
                type: Boolean,
                default: false
            },
            // Â∏∏ÊÄÅÊøÄÊ¥ª
            alwaysActive: {
                type: Boolean,
                default: false
            },
            clearable: {
                type: Boolean,
                default: false
            },
        },
        emits: ['update:modelValue'],
        data() {
            return {
                // uuid: Math.random().toString(36).substring(2)
                uuid: `input-${Math.random().toString(36).slice(2, 9)}`
            }
        }
    }
</script>

<style scoped>

    /* ÈáçÁΩÆÊ†∑Âºè */
    ::before, ::after {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    /* ÁªÑ‰ª∂‰∏ª‰Ωì */
    .input-group {
        position: relative;
        width: 100%;
        height: 100%;
    }

    /* ËæìÂÖ•Ê°Ü */
    .input {
        font-family: inherit;
        font-size: 18px;
        color: #132843;
        
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding: 0.5em;
        background-color: rgba(255, 255, 255);
        border: 3px solid #D6DEEB;
        border-radius: 0.5rem;
        outline: none;

        z-index: -1;

        transition: border-color 0.25s;
    }

    /* ÊèêÁ§∫Ê†áÁ≠æÂü∫Á°ÄÊ†∑Âºè */
    .title1, .title2 { 
        width: auto;
        max-width: 18rem;

        position: absolute;
        top: 0.75rem;
        left: 0.55rem;
        padding: 0 0.2rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        
        font-family: inherit;
        font-size: 18px;
        font-weight: 500;

        cursor: text;
        user-select: none;
        pointer-events: none;

        transform-origin: top left;
        transition: all 0.25s ease-in-out;
        /* transform: translateY(50%) */
    }

    /* Ê†áÁ≠æ1 ËæìÂÖ•Ê°ÜÊèêÁ§∫Ê†áÁ≠æ */
    .title1 {
        z-index: 2;
        color: #D6DEEB;
    }
    
    /* Ê†áÁ≠æ2 ËæπÊ°ÜÊèêÁ§∫Ê†áÁ≠æ */
    .title2 {
        z-index: 1;
        color: rgba(255, 255, 255, 0);
    }

    /* ËæπÊ°ÜÂç†‰Ωç */
    .border-gap {
        width: auto;
        max-width: 18rem;

        position: absolute;
        top: 0.75rem;
        left: 0.55rem;
        padding: 0 0.2rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;

        font-family: inherit;
        font-size: 18px;
        font-weight: 700;
        color: rgba(255, 255, 255, 0);

        cursor: text;
        user-select: none;
        pointer-events: none;

        transform-origin: top left;
        transform: translateY(-1.25rem) scale(0.75);
        transition: all 0.25s ease-in-out;

        z-index: 0;
    }

    /* ËæπÊ°ÜÂç†‰ΩçÂÖÉÁ¥† */
    .border-gap::before { 
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0.65rem;
        height: 5px;
        width: 100%;
        background-color: rgba(255, 255, 255);

        transform-origin: center center;
        transform: scaleX(0);
        transition: all 0.25s ease-in-out;
        z-index: -1;
    }

    /* ËæìÂÖ•Ê°ÜËé∑ÂèñÁÑ¶ÁÇπÊó∂ÁöÑÊ†∑Âºè */
    .input:focus, .input:valid,
    .is-always-active .input {
        border-color: #6191D3;
    }

    /* ËæìÂÖ•Ê°ÜËé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ1Ë°å‰∏∫ */
    .input:focus + .title1,
    .input:valid + .title1,
    .is-always-active .title1 {
        transform: translateY(-1.25rem) scale(0.75);
        color: rgba(255, 255, 255, 0);
    }

    /* ËæìÂÖ•Ê°ÜËé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ2Ë°å‰∏∫ */
    .input:focus ~ .title2,
    .input:valid ~ .title2,
    .is-always-active .title2 {
        transform: translateY(-1.25rem) scale(0.75);
        color: #6191D3;
        font-weight: 700;
        max-width: 23.9rem;
    }

    /* ËæìÂÖ•Ê°ÜËé∑ÂèñÁÑ¶ÁÇπÊó∂ËæπÊ°ÜÂç†‰ΩçË°å‰∏∫ */
    .input:focus ~ .border-gap,
    .input:valid ~ .border-gap,
    .is-always-active .border-gap {
        max-width: 24.1rem;
    }

    /* ËæìÂÖ•Ê°ÜËé∑ÂèñÁÑ¶ÁÇπÊó∂ËæπÊ°ÜÂç†‰ΩçÂÖÉÁ¥†Ë°å‰∏∫ */
    .input:focus ~ .border-gap::before,
    .input:valid ~ .border-gap::before,
    .is-always-active .border-gap::before {
        transform: scaleX(1);
    }

    /* ========== ÊàêÂäüÁä∂ÊÄÅÊ†∑Âºè ========== */
    /* - Â∏∏ÊÄÅ - */
    .input-group.status-success .input {
        border-color: #55e9bc;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπ - */
    .input-group.status-success .input:focus {
        border-color: #55e9bc;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ2ÁöÑÊ†∑Âºè - */
    .input-group.status-success .input:focus ~ .title2,
    .input-group.status-success .input:valid ~ .title2,
    .input-group.status-success.is-always-active .title2 {
        color: #55e9bc;
    }

    /* ========== Ë≠¶ÂëäÁä∂ÊÄÅÊ†∑Âºè ========== */
        /* - Â∏∏ÊÄÅ - */
    .input-group.status-warning .input {
        border-color: #ffd24b;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπ - */
    .input-group.status-warning .input:focus {
        border-color: #ffd24b;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ2ÁöÑÊ†∑Âºè - */
    .input-group.status-warning .input:focus ~ .title2,
    .input-group.status-warning .input:valid ~ .title2,
    .input-group.status-warning.is-always-active .title2 {
        color: #ffd24b;
    }

    /* ========== ÈîôËØØÁä∂ÊÄÅÊ†∑Âºè ========== */
    /* - Â∏∏ÊÄÅ - */
    .input-group.status-danger .input {
        border-color: #F44336;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπ - */
    .input-group.status-danger .input:focus {
        border-color: #F44336;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ2ÁöÑÊ†∑Âºè - */
    .input-group.status-danger .input:focus ~ .title2,
    .input-group.status-danger .input:valid ~ .title2,
    .input-group.status-danger.is-always-active .title2 {
        color: #F44336;
    }

    /* ========== Á¶ÅÁî®Áä∂ÊÄÅÊ†∑Âºè ========== */
    /* - Â∏∏ÊÄÅ - */
    .input-group.is-disabled .input {
        background-color: #f5f7fa !important;
        /* border-color: #e4e7ed !important; */
        border-color: #c0c4cc !important;
        color: #c0c4cc !important;
        cursor: not-allowed;
    }
    /* - Á¶ÅÁî®Êó∂Ê†áÁ≠æ1ÁöÑÊ†∑Âºè - */
    .input-group.is-disabled .title1 {
        color: #c0c4cc !important;
        cursor: not-allowed;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπ - */
    .input-group.is-disabled .input:focus + .title1,
    .input-group.is-disabled .input:valid + .title1 {
        transform: none;
        border-color: #c0c4cc !important;
    }
    /* - Ëé∑ÂèñÁÑ¶ÁÇπÊó∂Ê†áÁ≠æ2ÁöÑÊ†∑Âºè - */
    .input-group.is-disabled .input:focus ~ .title2,
    .input-group.is-disabled .input:valid ~ .title2 {
        display: none;
    }
    /* - Á¶ÅÁî®Êó∂ËæπÊ°ÜÂç†‰ΩçÂÖÉÁ¥†Ê†∑Âºè - */
    .input-group.is-disabled .border-gap::before { 
        background-color: #f5f7fa !important;
    }

    /* Â∏∏ÊÄÅÊøÄÊ¥ªÊ†∑Âºè */
    .is-always-active .title1 {
        opacity: 0 !important;
        transform: translateY(-1.25rem) scale(0.75);
    }

    /* Â∏∏ÊÄÅÊøÄÊ¥ªÂπ∂Á¶ÅÁî®Êó∂Ê†áÁ≠æ2ÁöÑÊ†∑Âºè */
    .input-group.is-disabled.is-always-active .title2 {
        color: #c0c4cc;
        transform: translateY(-1.25rem) scale(0.75);
    }

    .delete-btn { 
        width: 1.5rem;
        height: 1.5rem;
        /* z-index: 3; */

        border-radius: 50%;

        position: absolute;
        top: 0.7rem;
        right: 0.7rem;
    }

    #delete-icon { 
        width: 100%;
        height: 100%;

        fill: #6191D3;
        stroke: #6191D3;
    }













