import { createRouter, createWebHistory } from 'vue-router'
import BaseMainPage from '../components/BaseMainPage.vue'
import BaseAuthPage from '@/components/BaseAuthPage.vue'
import AuthLoginCard from '@/components/AuthLoginCard.vue'
import AuthRegiCard from '@/components/AuthRegiCard.vue'
import PageHome from '../components/PageHome.vue'
import PageInfo from '../components/PageInfo.vue'


const routes = [
  { path: '/', component: BaseMainPage, redirect: "/PageHome", 
    children: [
      { path: '/PageHome', component: PageHome },
      { path: '/PageInfo', component: PageInfo },
    ]
  },
  { path: '/', component: BaseAuthPage, redirect: "/PageLogin", 
    children: [
      { path: '/PageLogin', component: AuthLoginCard },
      { path: '/PageRegister', component: AuthRegiCard },
    ]
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router